import { BigNumber } from '@ethersproject/bignumber'
import { useMemo, useState } from 'react'

function getNested(theObject: any, path: string, separator = '.') {
  try {
    return path
      .replace('[', separator)
      .replace(']', '')
      .split(separator)
      .reduce((obj, property) => {
        return obj[property]
      }, theObject)
  } catch (err) {
    return undefined
  }
}

const useSortableData = (items: any, config: any = null) => {
  const [sortConfig, setSortConfig] = useState(config)
  const sortedItems = useMemo(() => {
    if (items && items.length > 0) {
      const sortableItems = [...items]
      if (sortConfig !== null) {
        sortableItems.sort((a, b) => {
          let aValue = getNested(a, sortConfig.key)
          let bValue = getNested(b, sortConfig.key)
          if(sortConfig.key === 'liquidity'){
            aValue = parseFloat(aValue)
            bValue = parseFloat(bValue)
          }
          else if(sortConfig.key === 'volume1d'){
            aValue = parseFloat(aValue)
            bValue = parseFloat(bValue)
          }
          else if(sortConfig.key === 'volume1w'){
            aValue = parseFloat(aValue)
            bValue = parseFloat(bValue)
          }
          else if(sortConfig.key === 'fees'){
             aValue = parseFloat(getNested(a, "volume1d")) * 0.003
             bValue = parseFloat(getNested(b, "volume1d")) * 0.003
          }
          if (aValue instanceof BigNumber && bValue instanceof BigNumber) {
            if (aValue.lt(bValue)) {
              return sortConfig.direction === 'ascending' ? -1 : 1
            }
            if (aValue.gt(bValue)) {
              return sortConfig.direction === 'ascending' ? 1 : -1
            }
          } else if (aValue === Infinity) {
            return sortConfig.direction === 'ascending' ? -1 : 1
          } else if (bValue === Infinity) {
            return sortConfig.direction === 'ascending' ? 1 : -1
          } else {
            if (aValue < bValue) {
              return sortConfig.direction === 'ascending' ? -1 : 1
            }
            if (aValue > bValue) {
              return sortConfig.direction === 'ascending' ? 1 : -1
            }
          }
          return 0
        })
      }
      return sortableItems
    }
    return []
  }, [items, sortConfig])

  const requestSort = (key: any, direction = 'ascending') => {
    if (sortConfig && sortConfig.key === key && sortConfig.direction === 'ascending') {
      direction = 'descending'
    } else if (sortConfig && sortConfig.key === key && sortConfig.direction === 'descending') {
      direction = 'ascending'
    }
    setSortConfig({ key, direction })
  }

  return { items: sortedItems, requestSort, sortConfig }
}

export default useSortableData
